@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:wght@300&display=swap');

@layer base {
    body {
        @apply font-[Raleway];

    }

    html {
        /* @apply snap-y;
        @apply snap-mandatory;
        @apply overflow-y-scroll; */
    }

    li {
        @apply px-4;
        @apply cursor-pointer;
    }
}

.content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
    transition: background-image ease;

}

.content-div:hover {
    background-image: linear-gradient(to bottom right, #411150, #945ba5) !important;
}

.link-underline::after {
    content: "";
    width: 0%;
    height: 3px;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #777;
    transition: width 0.2s;

}

.link-underline:hover::after {
    width: 100%;
}

.contact-form {
    border-width: 0px !important;
    border-bottom-width: 2px !important;
    border-color: white !important;
}

.contact-form:focus {
    outline: none;
}